@import "../../assets/styles/vars";

.cCb {
    box-sizing: border-box;
    font-family: Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 1.25;
    display: block;
    position: relative;
    min-height: 44px;
    padding-left: 40px;
    clear: left;

    &>label::before,
    &>input[type="checkbox"] {
        box-sizing: border-box;
        top: -2px;
        left: -2px;
        width: 44px;
        height: 44px;
    }

    &>input[type="checkbox"] {
        -webkit-font-smoothing: antialiased;
        cursor: pointer;
        position: absolute;
        z-index: 1;
        margin: 0;
        opacity: 0;
    }

    &>label {
        font-size: inherit;
        font-family: inherit;
        line-height: inherit;
        display: inline-block;
        margin-bottom: 0;
        padding: 8px 15px 5px;
        cursor: pointer;
        touch-action: manipulation;
    }

    &>label::before {
        content: "";
        position: absolute;
        border: 2px solid currentcolor;
        background: transparent;
    }

    &>input[type="checkbox"]:focus+label::before {
        border-width: 4px;
        outline: 3px dashed $outline-color;
    }

    &>label::after {
        box-sizing: content-box;
        content: "";
        position: absolute;
        top: 11px;
        left: 9px;
        width: 18px;
        height: 7px;
        transform: rotate(-45deg);
        border: solid;
        border-width: 0 0 5px 5px;
        border-top-color: transparent;
        opacity: 0;
        background: transparent;
    }

    &>input[type="checkbox"]:checked+label::after {
        opacity: 1;
    }
}
